body {
    -webkit-box-sizing: content-box;
            box-sizing: content-box;
    z-index: 0;
    font-family: 'Alegreya Sans', 'Droid Sans', Arial, Helvetica, sans-serif;
    font-size: 16px;
    height: 100%;
    width: 100%;
    min-height: 100%;
}

.App-header {
    background-color: #4040bf;
    height: 60px;
    padding-top: 10px;
    padding-bottom: 10px;
    color: white;
}

.hdr-col1,
.hdr-col2,
.hdr-col3 {
    margin: 0px;
    float: left;
    width: 33%;
    font-size: 1.4em;
    font-weight: 600;
    text-align: center;
    vertical-align: middle;
    height: 60px;
}

.hdr-col1 {
    font-size: 1.8em;
    font-weight: 800;
}

.instr-row {
    padding: 50px;
    text-align: center;
    height: 300px;
    background-image: url("./psdgraphics.jpg");
    color: #ffffff;
    font-weight: 900;
    font-size: 40px;
    vertical-align: middle;
}

.instr {
    font-weight: 700;
    font-size: 16px;
}

@media screen and (max-width:770px) {
    .App-header {
        height: 100px;
    }
}

.title {
    width: 100%;
    text-align: center;
    margin-bottom: 50px;
}


